import React, { useState } from "react";
import GaugeChart from "react-gauge-chart";

export default function Container() {

 const   roundToX=(num, decimals) =>{
    return +(Math.round(num + "e" + decimals) + "e-" + decimals);
  }
  const [bmiLabel, setBmiLabel] = useState("");
  const [bmival, setBmival] = useState(0);
  const [backgroundcolor, setbackgroundcolour] = useState('white');
  const bmi = () => {
    let weight = 0,
      height = 0;
    weight = document.getElementById("weight").value;
    height = document.getElementById("height").value;
    if (document.getElementById("weightUnit").value === "pounds") {
      weight = weight * 0.453592;
    }

    if (document.getElementById("heightUnit").value === "foot") {
      height = height * 0.3048;
    } else if (document.getElementById("heightUnit").value === "inch") {
      height = height * 0.0254;
    } else if (document.getElementById("heightUnit").value === "centimeter") {
      height = height * 0.01;
    }
    console.log(weight);
    let BMI = weight / (height * height);
    let str;

    if (BMI <= 15.9) {
      str = "Very Severaly Underweight";
    } else if (BMI >= 16 && BMI <= 16.9) {
      str = "Severaly Underweight";
    } else if (BMI >= 17 && BMI <= 18.4) {
      str = "Underweight";
    } else if (BMI >= 18.5 && BMI <= 24.9) {
      str = "Normal";
    } else if (BMI >= 25 && BMI <= 29.9) {
      str = "Overweight";
    } else if (BMI >= 30 && BMI <= 34.9) {
      str = "Obese className I";
    } else if (BMI >= 35 && BMI <= 39.9) {
      str = "Obese className II";
    } else if (BMI >= 40) {
      str = "Obese className III";
    }
    console.log(BMI);
    console.log(str);
    setBmiLabel(str);
    console.log(BMI/40);
    setBmival(BMI/40);
    let percent=(BMI/40)*100;
    console.log(percent);
    if(percent<47){

setbackgroundcolour('#e1b908')
    }
    else if(percent>47&&percent<60){
        setbackgroundcolour('#3cca0a')
    }
    else if(percent>60&&percent<100){
        setbackgroundcolour('#c92208')
    }
  };
  return (
    <div>
      <figure className="text-center my-5">
        <blockquote className="blockquote">
          <p className="fs-3 my-3">
            Weight loss doesnt begin in the gym with a dumbbell; it starts in
            your head with a decision.
          </p>
        </blockquote>
        <figcaption className="blockquote-footer">
          <cite title="Source Title"> Toni Sorenson</cite>
        </figcaption>
      </figure>

      <div className="container" style={{backgroundColor:backgroundcolor}}>
        <div className="box">
          <h2>BMI-Calculator</h2>
          <div className="containergau">
            <GaugeChart
             nrOfLevels={40}
             arcsLength={[0.45, 0.25, 0.4]}
             colors={['#F5CD19','#5BE12C', '#EA4228']}
             percent={bmival}
             arcPadding={0.02}
              id="gauge-chart1"
              textColor={"black"}
              style={{
                width: "100%",
                height: "8px",
              }}
            />
            <div className="labelgau ">
              <label>{bmiLabel}</label>
            </div>
          </div>
          <div className="label234">
            <div className="input-group">
              <label htmlFor="weight">Weight</label>
              <input type="text" id="weight" placeholder="Enter weight" />
              <select id="weightUnit">
                <option value="kg">Kg</option>
                <option value="pounds">Pounds</option>
              </select>
            </div>
            <div className="input-group">
              <label htmlFor="height">Height</label>
              <input type="text" id="height" placeholder="Enter height" />
              <select id="heightUnit">
                <option value="foot">Foot</option>
                <option value="metre">Metre</option>
                <option value="centimeter">Centimeter</option>

                <option value="inch">Inch</option>
              </select>
            </div>
             <div className="labelgau1">
              <label>BMI={roundToX((bmival*40), 4)}</label>
            </div>
           
            <button type="submit" onClick={bmi}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
